"use client";

import { useState } from "react";
import { useI18n } from "@/i18n/i18nProvider";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { ScrollArea } from "@/components/ui/scroll-area";

const BACKEND_URL = process.env.NEXT_PUBLIC_BACKEND_URL!;

type PreviewRow = {
  id: string;
  trackingNumber: string;
  companyName: string;
  provider: string;
  originCountry: string;
  destinationCountry: string;
  invoicedPrice: number;
  invoicedWeight: number | null;
};

type RawInvoice = {
  id?: string;
  shipment?: {
    trackingNumber?: string;
    provider?: string;
    originCountry?: string;
    destinationCountry?: string;
    company?: { name?: string };
  };
  invoicedPrice?: number | string;
  invoicedWeight?: number | string;
};

type UploadInvoicesDialogProps = {
  onUploaded?: () => void;
};

export function UploadInvoicesDialog({
  onUploaded,
}: UploadInvoicesDialogProps) {
  const { t } = useI18n();

  const [open, setOpen] = useState(false);
  const [file, setFile] = useState<File | null>(null);
  const [previewRows, setPreviewRows] = useState<PreviewRow[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [isParsing, setIsParsing] = useState(false);

  const resetState = () => {
    setFile(null);
    setPreviewRows([]);
    setError(null);
    setIsUploading(false);
    setIsParsing(false);
  };

  const handleOpenChange = (nextOpen: boolean) => {
    setOpen(nextOpen);
    if (!nextOpen) resetState();
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const selected = e.target.files?.[0] ?? null;
    setFile(selected);
    setPreviewRows([]);
    setError(null);

    if (!selected) return;

    try {
      setIsParsing(true);
      const text = await selected.text();
      const parsed = JSON.parse(text) as unknown;
      const arr: unknown[] = Array.isArray(parsed) ? parsed : [parsed];

      const rows: PreviewRow[] = arr.map((item, index) => {
        const raw = item as RawInvoice;
        const shipment = raw.shipment ?? {};
        const company = shipment.company ?? {};

        return {
          id: raw.id ?? `row-${index}`,
          trackingNumber: shipment.trackingNumber ?? "N/A",
          companyName: company.name ?? "Unknown",
          provider: shipment.provider ?? "N/A",
          originCountry: shipment.originCountry ?? "-",
          destinationCountry: shipment.destinationCountry ?? "-",
          invoicedPrice: Number(raw.invoicedPrice ?? 0),
          invoicedWeight:
            raw.invoicedWeight !== undefined
              ? Number(raw.invoicedWeight)
              : null,
        };
      });

      setPreviewRows(rows);
    } catch {
      setError(t("uploadDialog.invalidJson"));
    } finally {
      setIsParsing(false);
    }
  };

  const handleConfirmUpload = async () => {
    if (!file) {
      setError(t("uploadDialog.invalidJson"));
      return;
    }

    const formData = new FormData();
    formData.append("files", file);

    setIsUploading(true);
    setError(null);

    try {
      const res = await fetch(`${BACKEND_URL}/upload-invoices`, {
        method: "POST",
        body: formData,
      });

      if (!res.ok) {
        const text = await res.text();
        throw new Error(`Upload failed: ${res.status} ${text}`);
      }

      await res.json();
      setOpen(false);
      resetState();
      if (onUploaded) onUploaded();
    } catch {
      setError(t("uploadDialog.errorUpload"));
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogTrigger asChild>
        <Button
          className="rounded-full bg-[#00C3A0] hover:bg-[#009E85] text-white"
          size="sm"
        >
          {t("dashboard.uploadInvoices")}
        </Button>
      </DialogTrigger>

      <DialogContent className="max-w-3xl">
        <DialogHeader>
          <DialogTitle>{t("uploadDialog.title")}</DialogTitle>
          <DialogDescription>{t("uploadDialog.helperText")}</DialogDescription>
        </DialogHeader>

        <div className="space-y-3">
          {isParsing && (
            <Badge variant="outline">{t("uploadDialog.parsing")}</Badge>
          )}

          {error && (
            <div className="text-sm text-red-600 bg-red-50 border border-red-200 rounded-md px-3 py-2">
              {error}
            </div>
          )}

          {!isParsing && previewRows.length === 0 && !error && (
            <div className="text-sm text-slate-400 border border-dashed border-slate-200 rounded-md px-4 py-8 text-center">
              {t("uploadDialog.helperText")}
            </div>
          )}

          {previewRows.length > 0 && (
            <ScrollArea className="h-64 border border-slate-200 rounded-md">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Tracking #</TableHead>
                    <TableHead>{t("dashboard.company")}</TableHead>
                    <TableHead>Provider</TableHead>
                    <TableHead>Route</TableHead>
                    <TableHead className="text-right">Price</TableHead>
                    <TableHead className="text-right">Weight</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {previewRows.map((row) => (
                    <TableRow key={row.id}>
                      <TableCell>{row.trackingNumber}</TableCell>
                      <TableCell>{row.companyName}</TableCell>
                      <TableCell>{row.provider}</TableCell>
                      <TableCell>
                        {row.originCountry} → {row.destinationCountry}
                      </TableCell>
                      <TableCell className="text-right text-[#00997F] font-semibold">
                        {row.invoicedPrice.toFixed(2)}
                      </TableCell>
                      <TableCell className="text-right">
                        {row.invoicedWeight ?? "-"}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </ScrollArea>
          )}
        </div>

        <DialogFooter className="flex items-center justify-between gap-2">
          <label className="text-xs text-slate-500 flex items-center gap-2 cursor-pointer">
            <span className="inline-flex items-center rounded-full border border-slate-200 px-3 py-1 text-xs bg-slate-50">
              {file?.name ?? t("uploadDialog.pickFilePlaceholder")}
            </span>
            <input
              type="file"
              accept="application/json"
              className="hidden"
              onChange={handleFileChange}
            />
            <span className="text-[#00997F] text-xs underline">
              {t("uploadDialog.changeFile")}
            </span>
          </label>

          <div className="flex gap-2">
            <Button
              variant="outline"
              type="button"
              onClick={() => setOpen(false)}
            >
              {t("uploadDialog.cancel")}
            </Button>
            <Button
              type="button"
              onClick={handleConfirmUpload}
              disabled={isUploading || !file}
              className="bg-[#00C3A0] hover:bg-[#009E85] text-white"
            >
              {isUploading
                ? t("uploadDialog.uploading")
                : t("uploadDialog.confirm")}
            </Button>
          </div>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
